import React from 'react';
import { tencent, tsmc, amd, nvidia } from './imports';
import './brand.css';

const Brand = () => (
  <div className="gpt3__brand section__padding">
    <div>
      <img className="gpt3__brand-tencent" src={tencent} />
    </div>
    <div>
      <img className="gpt3__brand-tsmc" src={tsmc} />
    </div>
    <div>
      <img className="gpt3__brand-amd" src={amd} />
    </div>
    <div>
      <img className="gpt3__brand-nvidia" src={nvidia} />
    </div>
  </div>
);

export default Brand;
