import React from 'react';
import possibilityImage from '../../assets/christina.jpg';
import './possibility.css';

const Possibility = () => (
  <div className="gpt3__possibility section__padding" id="possibility">
    <div className="gpt3__possibility-image">
      <img src={possibilityImage} alt="possibility" />
    </div>
    <div className="gpt3__possibility-content">
      <h1 className="gradient__text">Collaboration <br /> enables innovation in the Web3 industry</h1>
      <p>Our collaboration with major industry tech players ensures that we can offer the insight that can give you an advantage in the Web3 space.</p>
    </div>
  </div>
);

export default Possibility;
