import React from 'react';
import circle from '../../assets/circle.png';
import './header.css';

const Header = () => (
  <div className="gpt3__header section__padding" id="home">
    <div className="gpt3__header-content">
      <h1 className="gradient__text"> Scaling GPU software development into Web3</h1>
      <p>Web3 and Blockchain present unique opportunities for harnessing the power of Graphics Processing Units for data analysis and modeling.</p>

      <div className="gpt3__header-content__input">
        <input type="email" placeholder="Your Email Address" />
        <button type="button">Get Started</button>
      </div>
    </div>

    <div className="gpt3__header-image">
      <img src={circle} />
    </div>
  </div>
);

export default Header;
