import React from 'react';
import Article from '../../components/article/Article';
import { blog01, blog02, blog03, blog04, blog05 } from './imports';
import './blog.css';

const Blog = () => (
  <div className="gpt3__blog section__padding" id="blog">
    <div className="gpt3__blog-heading">
      <h1 className="gradient__text">Great Changes are happening, <br /> We are in the news.</h1>
    </div>
    <div className="gpt3__blog-container">
      <div className="gpt3__blog-container_groupA">
        <Article imgUrl={blog01} date="Feb 1, 2022" text="GPU Acceleration and Blockchain. What innovations can we expect?" />
      </div>
      <div className="gpt3__blog-container_groupB">
        <Article imgUrl={blog02} date="Jan 8, 2022" text="How RX Group and Tencent are working to extend blockchain tech" />
        <Article imgUrl={blog03} date="Sep 26, 2021" text="AMD is leveraging CDNA architecture for Blockchain" />
        <Article imgUrl={blog04} date="Sep 26, 2021" text="RX Group and NVIDIA leverage Ampere Arch" />
        <Article imgUrl={blog05} date="Sep 26, 2021" text="RX Group closely consults with TSMC" />
      </div>
    </div>
  </div>
);

export default Blog;
