import React from 'react';
import Feature from '../../components/feature/Feature';
import './features.css';

const featuresData = [
  {
    text: 'We deploy the latest software technologies in conjunction with our software partner Tencent.',
  },
  {
    text: 'Our close collaboration with TSMC has ensured that we understand supply chain lifecycle of silicon chips',
  },
  {
    text: 'Working closely with AMD on CDNA and MI Instinct hardware for deployment on crypto and blockchain technologies.',
  },
  {
    text: 'Collaborated with NVIDIA on integrating CUDA acceleration for crypto and EVM based smart contracts.',
  },
];

const Features = () => (
  <div className="gpt3__features section__padding" id="features">
    <div className="gpt3__features-heading">
      <h1 className="gradient__text">Deployments in the Web3 space require expertise in order to transition effectively.</h1>
      <p>Contact Us for Free Trial Access to Get Started</p>
    </div>
    <div className="gpt3__features-container">
      {featuresData.map((item, index) => (
        <Feature title={item.title} text={item.text} key={item.title + index} />
      ))}
    </div>
  </div>
);

export default Features;
