import React from 'react';
import Feature from '../../components/feature/Feature';
import './whatGPT3.css';

const WhatGPT3 = () => (
  <div className="gpt3__whatgpt3 section__margin" id="wgpt3">
    <div className="gpt3__whatgpt3-feature">
      <Feature title="Who are RX Group" text="We are an EU/ Netherlands based consulting firm specializing in Web3 technologies and the blockchain. We work closely with major tech industry partners to deliver insights that give the leading advantage in the field." />
    </div>
    <div className="gpt3__whatgpt3-heading">
      <h1 className="gradient__text">The possibilities of Web3 are endless</h1>
      <p>Explore the Library</p>
    </div>
    <div className="gpt3__whatgpt3-container">
      <Feature title="Consultancy" text="We offer consultancy services on Web3 and Blockchain deployment." />
      <Feature title="Software Development" text="Our in house software developers are major contributors to open source technologies that power major cryptocurrencies" />
      <Feature title="Training" text="We offer training to small, medium and large businesses on transitioning from Web2 to Web3" />
    </div>
  </div>
);

export default WhatGPT3;
